<template>
  <div class="service">
    <div class="service_container">
      <div @click="openMenu" class="menu_bar">
        <p id="bar">我們的服務</p>
        <font-awesome-icon v-if="menuOpen == false" class="icon" :icon="['fal', 'angle-down']" />
        <font-awesome-icon v-if="menuOpen == true" class="icon" :icon="['fal', 'angle-up']" />
      </div>
      <div id="service_menu" class="menu_sec">
        <h3>我們的服務</h3>
        <router-link @click.native="active_item" class="menu" to="/services/">
          <span></span> 辦公室規劃設計工程
        </router-link>
        <router-link @click.native="active_item" class="menu" to="/services/service02">
          <span></span>專案廠房建置工程
        </router-link>
        <router-link @click.native="active_item" class="menu" to="/services/service03">
          <span></span>廠務各式修改維修工程
        </router-link>
        <router-link @click.native="active_item" class="menu" to="/services/service04">
          <span></span>無塵室產線各式加工件製品
        </router-link>
      </div>
      <router-view class="content"></router-view>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isOpen: true,
      menuOpen: false
    };
  },
  methods: {
    active_item: function(e) {
      var menu_list = document.getElementsByClassName("menu");
      var nowH = document.getElementById("service_menu");
      for (var i = 0; i < menu_list.length; i++) {
        menu_list[i].classList.remove("menu_act");
      }
      e.target.classList.add("menu_act");
      if (nowH.offsetHeight != 0) {
        nowH.style.height = "0";
        this.menuOpen = false;
      }
    },

    openMenu: function() {
      var nowH = document.getElementById("service_menu");

      if (nowH.offsetHeight == 0) {
        nowH.style.height = "100%";
        this.menuOpen = true;
      } else {
        nowH.style.height = "0";
        this.menuOpen = false;
      }
    }
  }
};
</script>

